<template>
  <div class="col-xs-3 col-sm-4 place-container">
    <router-link :to="{ name: 'place_details', params: { id: place.id }}">
      <div class="card card-album">
        <!--<img class="card-img-top" v-bind:src="album.cover.url">-->
        <img class="card-img-top" src="https://espanarusa.com/files/autoupload/77/21/51/ltcojfsx345156.jpg">
        <div class="card-block">
          <div class="card-title">{{place.title}}</div>
        </div>
      </div>
    </router-link>
  </div>
</template>

<script>
//  import { mapGetters } from 'vuex';

  export default {
    name: 'album_preview',
    props: ['place'],
//    computed: {
//      ...mapGetters([
//        'isAuthorized'
//      ])
//    },
    methods: {
    }
  };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .place-container {
    padding: 15px;
  }

  .card-block {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 35px;
    color: #fff;
    background-color: #000;
    opacity: 0.7;
  }

  .card-place {
    position: relative;
    cursor: pointer;
    min-height: 120px;
  }

  .card-title {
    text-align: center;
    vertical-align: middle;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .icon-remove {
    position: absolute;
    right: 10px;
    top: 8px;
    display: none;
    cursor: pointer;
    color: #ed4b4a
  }

</style>



