<template>
  <div id="app" class="main-content">
    <navbar></navbar>
    <router-view  class="container"></router-view>
    <vak-footer></vak-footer>
  </div>
</template>

<script>
  import Navbar from './components/Navbar.vue';
  import Footer from './components/Footer.vue';

export default {
  name: 'app',
  components: {
    'navbar': Navbar,
    'vak-footer': Footer
  }
};
</script>

<style>
  .main-content {
    padding-bottom: 60px;
  }
</style>



