<template>
  <nav class="main-navbar sticky-top">
    <div class="container">
      <div class="logo">
        <div class="logo-title">Вакол</div>
        <div class="logo-sub-title">Стежкамi роднай краiны</div>
      </div>
      <div class="menu">
        <div class="menu-item">Места
          <div class="sub-items">
            <div class="sub-item">First</div>
            <div class="sub-item">Second</div>
          </div>
        </div>
        <div class="menu-item">Маршруты
          <div class="sub-items">
            <div class="sub-item">First</div>
            <div class="sub-item">Second</div>
          </div>
        </div>
        <div class="menu-item">О нас
          <div class="sub-items">
            <div class="sub-item">First</div>
            <div class="sub-item">Second</div>
          </div>
        </div>
      </div>
    </div>
  </nav>
</template>

<script>

  export default {
    name: 'navbar'
  };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

  .main-navbar {
    height: 60px;
    background-color: #26628A;
    color: #fff;
  }

  .logo {
    float: left;
    padding-top: 10px;
  }

  .logo-title {
    line-height: 1;
    font-size: 1.6rem;
  }

  .logo-sub-title {
    font-size: 0.7rem;
  }

  .menu {
    float: right;
    font-size: 0.8rem;
    padding-top: 20px;
  }

  .menu-item {
    text-transform: uppercase;
    border-left: solid 1px #fff;
    border-right: solid 1px #fff;
    padding: 0 15px;
    display: inline-block;
    position: relative;
  }

  .menu-item:first-child {
    border: none;
  }

  .menu-item:last-child {
    border: none;
  }

  .sub-items {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #D9F1FA;
    width: 150px;
  }

  .menu-item:hover .sub-item {
    display: block;
    color: #f11e1c;
  }
</style>



