<template>
  <div class="container">
    <ul class="st-breadcrumbs">
      <li class="st-breadcrumbs-item" v-for="breadcrumb in data">
        <a class="breadcrumb-link">{{breadcrumb.title}}</a>
        <span class="splitter">/</span>
      </li>
    </ul>
  </div>
</template>

<script>

  export default {
    props: {
      data: Array
    }
  };

</script>

<style lang="scss">
  @import "../styles/colors";

  .st-breadcrumbs {
    list-style-type: none;
    padding: 10px 0;
    margin-bottom: 0;
  }

  .st-breadcrumbs-item {
    display: inline-block;
    padding-right: 5px;
    color: $c-muted;

    &:last-child {
      color: $c-primary;

      .splitter {
        display: none;
      }
    }
  }

  .st-breadcrumbs-item:last-child .fa {
    display: none;
  }

  .splitter {
    color: #26628A;
    font-weight: 600;
  }
</style>



