import Vue from 'vue';
import Router from 'vue-router';
import Home from '@/containers/Home';
import PlaceDetails from '@/containers/PlaceDetails';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    {
      path: '/home',
      name: 'home',
      component: Home
    },
    {
      path: '/place/:id',
      name: 'placeDetails',
      component: PlaceDetails
    }
  ]
});



